<template>
  <div id="home">
    <home-top />
    <tab-control
      class="placeHolder"
      v-show="showPlaceHolder"
      :titles="['', '', '']"
      ref="tabControl1"
      @tabClick="tabClick"
    />
    <scroll
      class="scroll"
      ref="scroll"
      @scroll="scroll"
      @pullingUp="pullingUp"
    >
      <home-swiper :banner="banner" @SwiperHasLoad="SwiperHasLoad" />
      <home-reco :recommends="recommend" />
      <home-weekly />
      <tab-control
        :titles="['', '', '']"
        ref="tabControl2"
        @tabClick="tabClick"
      />
      <good-list :goods="curGoods" />
    </scroll>
    <back-top @click.native="backTop" v-show="isShowBT" />
  </div>
</template>

<script>
import HomeTop from "./childComps/HomeTop";
import Scroll from "components/common/scroll/Scroll";
import HomeSwiper from "./childComps/HomeSwiper";
import HomeReco from "./childComps/HomeReco";
import HomeWeekly from "./childComps/HomeWeekly";
import TabControl from "components/content/TabControl";
import GoodList from "components/content/goods/GoodList";
import { backTopMixin } from "common/mixin";

import data from "./data.js";
import methods from "./methods.js";

import { debounce } from "common/utils";

import { MultiData } from "network/home";

export default {
  name: "Home",
  components: {
    HomeWeekly,
    HomeTop,
    Scroll,
    HomeSwiper,
    HomeReco,
    TabControl,
    GoodList,
  },
  data,
  methods,
  mixins: [backTopMixin],
  computed: {
    //  goodListtype goods goodsListItem
    // typeHome tabControlthis.curType
    // curType curGoodsgoodList
    curGoods() {
      // ListItemitem ListItemitem.img
      // item.price item.coll goodList
      this.goods[this.curType].list.forEach((v) => {
        v.img = v.show.img;
        v.coll = v.cfav;
      });
      return this.goods[this.curType].list;
    },
  },
  created() {
    // 
    MultiData().then((response) => {
      this.banner = response.data.banner;
      this.recommend = response.data.recommend;
    });
    // 
    for (let type in this.goods) {
      this.getGoodData(type);
    }
  },
  mounted() {
    let scroll = this.$refs.scroll;
    let refresh = debounce(50, scroll.refresh);
    this.itemImgLoad = () => {
      refresh("Home");
    };
  },
  activated() {
    let scroll = this.$refs.scroll;
    this.$bus.$on("itemImgLoad", this.itemImgLoad);
    scroll.refresh();
    scroll.scrollTo(this.position,0);
  },
  deactivated() {
    this.position = {x:0,y:this.$refs.scroll.scroll.y}
    this.$bus.$off("itemImgLoad", this.itemImgLoad);
  },
};
</script>

<style scoped>
#home {
  height: 100vh;
}

.placeHolder {
  position: fixed;
  top: 44px;
  z-index: 10;
  left: 0;
  right: 0;
}

.scroll {
  position: absolute;
  top: 44px;
  bottom: 49px;
  left: 0;
  right: 0;
  /*  */
  overflow: hidden;
}
</style>
