<template>
  <div class="swiper">
    <div
      class="wrapper"
      ref="wrapper"
      :style="wrapperStyle"
      @touchstart="touchstart"
      @touchmove="touchmove"
      @touchend="touchend"
    >
      <slot></slot>
    </div>
    <div
      class="indicator"
      v-show="count > 3 && showIndicator"
      :style="indicatorStyle"
    >
      <slot name="indicator">
        <div
          class="indicator-icon"
          :class="iconClass(num)"
          v-for="num in count - 2"
          :key="num"
        ></div>
      </slot>
    </div>
  </div>
</template>

<script>
let methods = {
  touchstart(e) {
    if (this.scrolling) {
      this.moving = false;
      this.callback = () => {
        this.touchstart(e);
      };
      return;
    }
    this.moving = true;
    this.preX = e.changedTouches[0].clientX;
    this.startLeft = this.left;
    //  touchmove
    this.dTransTime = 0;
    this.stopTimer();
  },
  touchmove(e) {
    if (!this.moving) {
      return;
    }
    //  
    const mouseMove = e.changedTouches[0].clientX - this.preX;
    this.preX = e.changedTouches[0].clientX;
    // left
    this.left = this.left + mouseMove;
    // index
    this.check();
  },
  // index indexleft
  check() {
    // index  -this.left = -0.00000123
    // -this.left / this.width  0
    this.index = parseInt(-this.left.toFixed(2) / this.width);
    // this.left >= 0
    //   

    // this.left <= -(this.count - 1) * this.width
    //   
    //  === 
    if (this.left >= 0 || this.left <= -(this.count - 1) * this.width) {
      this.index = this.index === 0 ? this.count - 2 : 1;
      // touchstart 
      this.dTransTime = 0;
      this.left = -this.index * this.width;
    }
  },
  touchend() {
    if (!this.moving) {
      return;
    }
    this.callback = null;
    // move ratio 
    let distance = (this.left - this.startLeft) % this.width;
    // left
    // distance < 0 
    // indexindex scrollTo(1) scrollTo(0)
    // distance > 0 
    // indexindex scroll(0) scroll(1)
    if (Math.abs(distance) >= this.width * this.ratio) {
      distance < 0 ? this.scrollTo(1) : this.scrollTo(0);
    } else if (distance !== 0) {
      distance < 0 ? this.scrollTo(0) : this.scrollTo(1);
    }
    this.startTimer();
  },
  // leftindex+directionleft
  scrollTo(direction) {
    this.curIndex = this.index;
    // 
    this.index += direction;
    // left
    let end = -this.index * this.width;
    // 
    this.dTransTime = (Math.abs(this.left - end) / this.width) * this.transTime;
    //  
    this.scrolling = true;
    // left 
    this.left = end;
    // 
    setTimeout(() => {
      // 
      this.scrolling = false;
      // index0           0
      // indexlength-1       length - 2
      if (this.index === 0 || this.index === this.count - 1) {
        this.index = this.index === 0 ? this.count - 2 : 1;
        //  0
        this.dTransTime = 0;
        // left
        this.left = -this.index * this.width;
      }
      // index
      this.curIndex = this.index;
      this.callback && this.callback();
    }, this.dTransTime);
  },
  // 
  stopTimer() {
    clearInterval(this.timer);
  },
  startTimer() {
    this.timer = setInterval(() => {
      this.scrollTo(1);
    }, this.interval);
  },
  // class
  iconClass(index) {
    return { active: this.curIndex === index };
  },
  init() {
    // 
    let wrapper = this.$refs.wrapper;
    if (wrapper.childElementCount > 1) {
      let firstChild = wrapper.firstElementChild.cloneNode(true);
      let lastChild = wrapper.lastElementChild.cloneNode(true);
      wrapper.appendChild(firstChild);
      wrapper.insertBefore(lastChild, wrapper.firstElementChild);

      this.count = wrapper.childElementCount;
      // Swiper
      this.width = parseInt(getComputedStyle(this.$el).width);
      wrapper.style.width = this.width * this.count + "px";

      // -width px
      this.left = -this.width;
      this.startTimer();
      // dTransTime  left
    }
  },
};
export default {
  methods,
  name: "Swiper",
  props: {
    // 
    interval: {
      type: Number,
      default: 3000,
    },
    // 
    transTime: {
      type: Number,
      default: 300,
    },
    // 
    showIndicator: {
      type: Boolean,
      default: true,
    },
    // 
    ratio: {
      type: Number,
      default: 0.25,
    },
    indicatorStyle: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  data() {
    return {
      // swiper
      width: 0,
      // 
      count: 2,
      // 
      index: 1,
      // listleft
      left: 0,
      // 
      scrolling: false,
      // () 
      curIndex: 1,
      dTransTime: 0,
      //  touchstart
      moving: false,
      // scrollTo scrollingtouchstart
      callback: null,
    };
  },
  computed: {
    // list
    wrapperStyle() {
      return {
        left: this.left + "px",
        transition: `left ease-in-out ${this.dTransTime}ms`,
      };
    },
  },
};
</script>

<style scoped>
.swiper {
  position: relative;
  overflow: hidden;
}

.wrapper {
  display: flex;
  position: absolute;
}

.indicator {
  position: absolute;
  z-index: 20;
  display: flex;
  left: 5%;
  bottom: 5%;
}

.indicator-icon {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.3);
  border: 2px solid transparent;
  background-clip: content-box;
}

.indicator-icon.active {
  background-color: rgba(255, 255, 255, 0.8);
  border: 2px solid rgba(255, 255, 255, 0.3);
}
</style>